@font-face {
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-family: "Jaapokkisubtract";
  src: url("../app/fonts/jaapokkisubtract.woff2");
}
@font-face {
  font-weight: 400;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue";
  src: url("../app/fonts/helveticaneue.woff2");
}
@font-face {
  font-weight: 300;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Light";
  src: url("../app/fonts/helveticaneue-light.woff2");
}
@font-face {
  font-weight: 500;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Medium";
  src: url("../app/fonts/helveticaneue-medium.woff2");
}
@font-face {
  font-weight: 700;
  font-display: swap;
  font-style: normal;
  font-family: "HelveticaNeue Bold";
  src: url("../app/fonts/helveticaneue-bold.woff2");
}
:root {
  --font-body: "HelveticaNeue Bold", sans-serif;
  --font-title: "Jaapokkisubtract", sans-serif;
  --transition: .2s;
}

:root {
  --special: hsl(229, 86%, 58%);
  --text: hsl(0, 0%, 10%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  font-family: var(--font-title);
}

a,
a:visited,
a:active {
  text-decoration: none;
  transition: var(--transition) ease-in-out;
}

.container {
  padding: 0 1.25rem;
  max-width: 73.75rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    transform: translate(0, 0);
    padding: 0;
  }
}